.hidden{
    visibility: hidden;
}
.active{

        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        align-items: center;
        justify-content: center;
        visibility: visible;
        opacity: 1;
        transition: visibility 0.5s, opacity 0.5s linear; 
        z-index: 1;
}
.overlay {
    background-color: rgba(0, 0, 0, 0.8);
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 3;
  }
  .container {
    max-width: 358px;
    background-color: white;
    position: relative;
    top: 50%;
    left: 50%;
    border-radius: 4%;
    transform: translate(-50%, -50%);
    padding-left: 36px;
    padding-right: 36px;
  }
  
  @media screen and (max-width: 550px) {
    .container {
      padding-left: 22px;
      padding-right: 22px;
      max-width: 290px;
    }
  }
  .btnClose {
    position: fixed;
    top: -40px;
    right: -40px;
    background-image: url("../../images/x.png");
    background-repeat: no-repeat;
    background-size: 40px;
    box-sizing: border-box;
    background-color: transparent;
    width: 40px;
    height: 40px;
    border: none;
  }
  
  .btnClose:hover {
    opacity: 0.6;
    cursor: pointer;
  }
  .text{
    text-align: center;
    font-family: 'Oxygen', sans-serif;
    font-weight: 400;
    font-size: 18px;
  }
  

